  padding:30px 15px;
}


.margin-top-control{
  margin-top: 50px;
}
.text-right { 
  margin-top: 50px;
  text-Align: right;
  color: white;
}

.text-left { 
  margin-top: 50px;
  text-Align: left;
}

.contenttable {
  width:350px;border-collapse:collapse
}
.contenttable td {
  border-bottom:1px solid #dcdac2;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

 .main-title {
  width: 100px;
  height : 50px;
  object-fit: cover;
  background-image: url('./img/innotreetitle.png');
  background-size: cover;
} 
.sub-title-1 { 
  height: 300px;
  background-image: url('./img/bg.png');
  background-size: cover;
  background-position: center;
}
.sample-img { 
  height: 198px;
  background-size: cover;
  background-position: center;
}
.sample2-img { 
  
  width: 200px;
  height: 150px;
  background-size: cover;
  background-position: center;
  background-position: center;
}
.horizonline { 
  text-Align: center;
  border-Bottom: 1px solid #aaa;
  line-Height: 0.1em;
  margin: 100px 0 20px;
}
.horizonline2 { 
  text-Align: center;
  border-Bottom: 1px solid #aaa;
  line-Height: 0.1em;
  margin: 30px 0 20px;
}
.imgcontrol {

  width : 400px;
  height : 250px;
}

.disabledcontent { 

  margin: 100px 200px;
}
.floatleft { 

  float:left;
}

.title { 
  text-Align: center;
  margin-top: 50px;
}
.title2 { 
  text-Align: center;
  margin-top: 20px;
}


.box-id {
  background-color: #eee; 
  display:block;
  padding:6px 15px;
  margin-top: 10px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
